import React from "react";
import Card from "./Card";

const CardList = ({ robots }) => {
  // if(true) throw new Error('NOOO');
  return (
    <>
      {robots.map(user => (
        <Card
          key={user.id}
          id={user.id}
          name={user.name}
          email={user.email}
        />
      ))}
    </>
  );
};

export default CardList;
